import "../scss/TopImage.scss";
import PhotoTop from '@/images/pageArticle/PhotoTop.png'
import { articleData } from "./ArticleDate";
import Line from '@/images/pageArticle/line.svg'
import { Link } from "react-router-dom";
import { useEffect } from "react";

const TopImage = () => {
  const article = articleData[0];
  useEffect(() => {
    const articleBtnBack = document.querySelector('.article_btn_back')! as HTMLElement;
    const articleImg = document.querySelector('.article-img')! as HTMLElement;
    const articleDate = document.querySelector('.article-date')! as HTMLElement;

    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
        opacity: 1,
        duration: 0.2,
        delay: delay, // задержка для каждой буквы
        ease: "power1.out"
      });
    }

    const objEl = {
      articleBtn: [articleBtnBack, 0.1],
      articleImg:[articleImg, 0.2],
      articleD:[articleDate, 0.3],
    
  }
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  Object.entries(objEl).forEach(([_, value]) => {
     
      const obj = value[0] as HTMLElement;     
      const num = value[1] as number;
      activeScrollTrigger(obj, num);
  });
    function activeScrollTrigger(obj: HTMLElement, delay: number) {

      obj.style.opacity = '0'

      ScrollTrigger.create({
        trigger: obj,
        start: `100 80%`,
        end: `0 5%`,
        /*  markers: true, */
        onEnter: () => {
          animHead(obj, delay)
        },


      });
    }
  }, []);
  return (
    <>
      <Link className="article_btn_back" to='/blog'>
        <img className="article_btn_back_img" src={Line} alt="img" />
        Back
      </Link>
      <div className="article-top-image">

        <img src={PhotoTop} alt={article.introductionTitle} className="article-img"/>
        <time dateTime="2024-04-14" className="article-date">
          {article.date}
        </time>
      </div>
    </>
  );
};

export default TopImage;
