import "../scss/ArticleContent.scss";
import RightBracket from "@/uiKit/RightBracket";
import LeftBracket from "@/uiKit/LeftBracket";
import SocialIcons from "@/uiKit/SocialIcons";
import ArticleSlider from "./ArticleSlider";
import { articleData } from "./ArticleDate";
import { useEffect } from "react";



const ArticleContent: React.FC = () => {
  const article = articleData[0];
  useEffect(() => {
    const socialIcons = document.querySelector('.social-icons')! as HTMLElement;
    const articleCategory = document.querySelector('.article-category-container')! as HTMLElement;
    const articleMainTitle = document.querySelector('.article-main-title')! as HTMLElement;
    const articleMainDescription = document.querySelector('.article-main-description')! as HTMLElement;
    const introductionTitle = document.querySelector('.introduction-title')! as HTMLElement;
    const introductionDescription = document.querySelector('.introduction-description')! as HTMLElement;
    const articleSliderContainer = document.querySelector('.article-slider-container')! as HTMLElement;
    const articleParagraphContent = document.querySelector('.article-paragraph-content')! as HTMLElement;
    const elements = articleParagraphContent.querySelectorAll<HTMLElement>('h1, h2, h3, p');

    // Запускаем функцию для каждого элемента
    elements.forEach((element, index) => {
      activeScrollTrigger(element, index * 0.1);
    });

    const animHead = (heading: HTMLElement, delay: number) => {

      gsap.to(heading, {
        opacity: 1,
        duration: 0.2,
        delay: delay, // задержка для каждой буквы
        ease: "power1.out"
      });
    }

    const objEl = {
      icons: [socialIcons, 0.2],
      header: [articleCategory, 0.2],
      title: [articleMainTitle, 0.2],
      des: [articleMainDescription, 0.2],
      title_2: [introductionTitle, 0.2],
      des_2: [introductionDescription, 0.2],
      sliders: [articleSliderContainer, 0.2],
       
    
  }
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  Object.entries(objEl).forEach(([_, value]) => {
     
      const obj = value[0] as HTMLElement;     
      const num = value[1] as number;
      activeScrollTrigger(obj, num);
  });

    function activeScrollTrigger(obj: HTMLElement, delay: number) {

      obj.style.opacity = '0'

      ScrollTrigger.create({
        trigger: obj,
        start: `100 80%`,
        end: `0 5%`,
        /*  markers: true, */
        onEnter: () => {
          animHead(obj, delay)
        },


      });
    }
  }, []);
  return (
    <main className="article-content-container">
      <article className="article-content">
        <header className="article-header">
          <div className="article-category-container">
            <LeftBracket />
            <span className="article-category">{article.category}</span>
            <RightBracket />
          </div>
          <h1 className="article-main-title">{article.mainTitle}</h1>
          <p className="article-main-description">{article.mainDescription}</p>
        </header>
        <section className="article-introduction-content">
          <h2 className="introduction-title">{article.introductionTitle}</h2>
          <p className="introduction-description">
            {article.introductionDescription}
          </p>
        </section>

        <ArticleSlider />

        <section className="article-paragraph-content">
          <h3 className="paragraph-title">{article.paragraphTitle1}</h3>
          <p className="paragraph-description">
            {article.paragraphDescription1}
          </p>

          <h3 className="paragraph-title">{article.paragraphTitle2}</h3>
          <p className="paragraph-description">
            {article.paragraphDescription2}
          </p>

          <h2 className="closing-title">{article.closingTitle1}</h2>
          <p className="paragraph-description">
            {article.paragraphDescription3}
          </p>

          <h2 className="closing-title">{article.closingTitle2}</h2>
          <p className="paragraph-description">
            {article.paragraphDescription4}
          </p>
        </section>
      </article>
      <SocialIcons />
    </main>
  );
};

export default ArticleContent;
