import "../scss/SeeMoreSection.scss";
import LeftBracket from "@/uiKit/LeftBracket";
import RightBracket from "@/uiKit/RightBracket";
import PhotoTop from '@/images/pageArticle/PhotoTop.png'
import { useEffect, useRef } from "react";
const newsList = [
  {
    id: 1,
    category: "News",
    title: "Immersive Experience Design: Expert Insights and Techniques",
    date: "14 April 2024",
    imageUrl: PhotoTop,
  },
  {
    id: 2,
    category: "News",
    title: "Immersive Experience Design: Expert Insights and Techniques",
    date: "15 April 2024",
    imageUrl: PhotoTop,
  },
];

const SeeMoreSection = () => {
  const seeMoreLabelRef = useRef<HTMLSpanElement>(null);
  const seeMoreNewsListRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const seeMoreLabel = seeMoreLabelRef.current;
    const seeMoreList = seeMoreNewsListRef.current;

    if (!seeMoreLabel || !seeMoreList) return;
    const seeMoreItems = seeMoreList.querySelectorAll<HTMLElement>('.see-more-news-item');

    const animHead = (heading: HTMLElement, delay: number) => {
      gsap.to(heading, {
        opacity: 1,
        duration: 0.2,
        delay: delay,
        ease: "power1.out",
      });
    };

    const activeScrollTrigger = (obj: HTMLElement, delay: number) => {
      obj.style.opacity = '0';
      ScrollTrigger.create({
        trigger: obj,
        start: "100% 80%", // Use percentages for consistent behavior
        end: "100% 5%",
        onEnter: () => animHead(obj, delay),
        //markers: true, // Remove this in production
      });
    };

    seeMoreItems.forEach((element, index) => {
      activeScrollTrigger(element, index * 0.1);
    });

    const text = seeMoreLabel.textContent!;
    seeMoreLabel.innerHTML = '';

    text.split('').forEach((letter) => {
      const span = document.createElement('span');
      span.textContent = letter;
      span.style.opacity = '0';
      seeMoreLabel.appendChild(span);
    });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: seeMoreLabel,
        start: "0 80%",
        end: "0 75%",
        scrub: 0.5, // Smooth transition
        markers: false, // Remove markers in production
        invalidateOnRefresh: true,
      },
    });

    tl.to(seeMoreLabel.querySelectorAll('span'), {
      opacity: 1,
      duration: 0.5,
      stagger: 0.05,
      ease: "power1.out",
    });


  }, []);
  return (
    <section className="see-more-section">
      <span className="see-more-label" ref={seeMoreLabelRef}>See more</span>
      <div className="see-more-news-list" ref={seeMoreNewsListRef}>
        {newsList.map((news) => (
          <article key={news.id} className="see-more-news-item">
            <div className="see-more-image-container">
              <img
                src={news.imageUrl}
                alt={news.title}
                className="see-more-image"
              />
            </div>
            <header className="see-more-header">
              <div className="see-more-category-container">
                <LeftBracket />
                <span className="see-more-category">{news.category}</span>
                <RightBracket />
              </div>
              <h2 className="see-more-title">{news.title}</h2>
            </header>
            <time className="see-more-date">{news.date}</time>
          </article>
        ))}
      </div>
    </section>
  );
};

export default SeeMoreSection;
